import request from '@/utils/request'

export default {
  // 验证登录信息
  login: async function (data) {
    return request({
      url: '/login/valid',
      method: 'post',
      data
      // headers:{'Content-Type': 'application/x-www-form-urlencoded' }
    })
  },
  // 注册
  register: async function (data) {
    return request({
      url: '/users/add',
      method: 'post',
      data
      // headers:{'Content-Type': 'application/x-www-form-urlencoded' }
    })
  },
  // 所有用户
  getAllUser: async function (params) {
    return request({
      url: '/users/alluser',
      method: 'get',
      params
      // headers:{'Content-Type': 'application/x-www-form-urlencoded' }
    })
  },
  // 删除用户
  deleteUser: async function (id) {
    return request({
      url: '/users/delete?id=' + id,
      method: 'get'
      // headers:{'Content-Type': 'application/x-www-form-urlencoded' }
    })
  },
  // 更新密码
  updataPwd: async function (id, password) {
    return request({
      url: `/users/changepwd?id=${id}$password=${password}`,
      method: 'get'
      // headers:{'Content-Type': 'application/x-www-form-urlencoded' }
    })
  },
  // 更新状态
  updataState: async function (id, state) {
    return request({
      url: `/users/changestate?id=${id}$state=${state}`,
      method: 'get'
      // headers:{'Content-Type': 'application/x-www-form-urlencoded' }
    })
  },
  // 更改角色
  updataRole: async function (id, role) {
    return request({
      url: `/users/changerole?id=${id}$password=${role}`,
      method: 'get'
      // headers:{'Content-Type': 'application/x-www-form-urlencoded' }
    })
  },
  // 查询用户信息
  userinfo: async function (id) {
    return request({
      url: '/users/userinfo?id=' + id,
      method: 'get'
      // headers:{'Content-Type': 'application/x-www-form-urlencoded' }
    })
  }
}
