<template>
  <div style="width: 95%; margin: 20px auto">
    <el-row :gutter="10">
      <el-col :span="24">
        <div class="grid-content bg-purple">
          <el-input placeholder="请输入内容" v-model="searchValue" style="width: 27%">
            <el-button slot="append" icon="el-icon-search" @click="handleSearchUser"></el-button>
          </el-input>
          <el-button type="primary" class="ml-10" @click="handleReset">重置</el-button>
          <el-button type="primary" class="ml-10" @click="handleDialogVisible('add', null)">添加</el-button>
          <!--   <span style="float:right">
            <el-button type="primary" @click="handleDisable">禁用</el-button>
            <el-button type="primary" @click="handleUpdataPwn" class="ml-10">修改密码</el-button>
          </span>-->
        </div>
      </el-col>
    </el-row>
    <hr />
    <div class="table-container">
      <el-table v-loading="loading" ref="multipleTable" :default-sort="{ prop: 'role' }" highlight-current-row :data="currentTableData" tooltip-effect="dark" style="width: 100%" border>
        <el-table-column type="index" width="50" align="center" />
        <el-table-column prop="user_name" label="用户名" show-overflow-tooltip />
        <el-table-column prop="email" label="邮箱" show-overflow-tooltip />
        <el-table-column prop="role" label="角色" align="center">
          <template slot-scope="scope">
            <el-tag :type="scope.row.role === 'user' ? '' : 'success'" effect="dark">{{ scope.row.role === 'user' ? '普通用户' : '管理员' }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="state" label="状态" width="150" align="center">
          <template slot-scope="scope">
            <el-tag type="danger" v-if="!scope.row.state">禁用</el-tag>
            <el-tag type="success" v-else>启用</el-tag>
          </template>
        </el-table-column>

        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <!-- <el-button
              size="mini"
              type="primary"
              @click="handleDialogVisible('edit', scope.row, scope.$index)"
              >编辑</el-button
            > -->
            <el-dropdown @command="handleCommand($event, scope.row)">
              <el-button type="primary" size="mini">编辑</el-button>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="edit">修改密码</el-dropdown-item>
                <el-dropdown-item command="role">切换角色</el-dropdown-item>
                <el-dropdown-item command="state">
                  <span v-if="scope.row.state === 0" style="color: #67c23a">状态启用</span>
                  <span v-else style="color: red">状态禁用</span>
                </el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <el-button class="ml-10" size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination background :page-size="10" style="float: right; margin-top: 30px" :current-page.sync="currentPage" @current-change="handleCurrentChange" layout="total, prev, pager, next, jumper" :total="tableData.length"> </el-pagination>
    </div>
    <!-- 添加 -->
    <el-dialog :title="title" :visible.sync="centerDialogVisible" width="30%" top="20vh" center :close-on-click-modal="false">
      <el-form :model="loginForm" :rules="loginRules" ref="loginForm" label-position="left">
        <el-form-item prop="user_name">
          <el-input name="user_name" type="text" v-model="loginForm.user_name" placeholder="请输入用户名">
            <span slot="prefix"> </span>
          </el-input>
        </el-form-item>
        <el-form-item prop="email">
          <el-input name="email" v-model="loginForm.email" placeholder="请输入邮箱" type="email">
            <span slot="prefix"> </span>
          </el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input name="password" :type="pwdType" @keyup.enter.native="handleChangeInfo" v-model="loginForm.password" placeholder="请输入密码">
            <span slot="prefix"> </span>
            <span slot="suffix" @click="showPwd" v-if="pwdType"> </span>
            <span slot="suffix" @click="showPwd" v-else> </span>
          </el-input>
        </el-form-item>

        <el-form-item style="text-align: center">
          <span style="position: relative; top: 2px; right: 8px">状态</span>
          <el-switch v-model="loginForm.state" active-color="#13ce66" inactive-color="#ff4949" :inactive-value="0" :active-value="1" disabled></el-switch>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click.native.prevent="handleChangeInfo">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 修改密码 -->
    <el-dialog title="修改密码" :visible.sync="pwnDialogVisible" width="30%" center :close-on-click-modal="false">
      <el-input type="password"> </el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="pwnDialogVisible = false">取 消</el-button>
        <el-button type="primary">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { isvalidUsername } from '@/utils/validate'
import { deepClone } from '@/utils/index'
import user from '@/api/user'
export default {
  components: {},
  data () {
    const validateUsername = (rule, value, callback) => {
      if (!isvalidUsername(value)) {
        callback(new Error('输入数字,字母或下划线,长度大于3位'))
      } else {
        callback()
      }
    }
    const validatePass = (rule, value, callback) => {
      if (value.length < 4) {
        callback(new Error('密码不能小于4位'))
      } else {
        callback()
      }
    }
    const validateEmail = (rule, value, callback) => {
      const emailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
      if (!emailReg.test(value)) {
        callback(new Error('请输入正确格式的邮箱地址'))
      } else {
        callback()
      }
    }
    return {
      searchValue: '',
      centerDialogVisible: false,
      pwnDialogVisible: false,
      title: '注册用户',
      loginForm: {
        user_name: '',
        password: '',
        email: '',
        state: 1
      },
      loginRules: {
        user_name: [
          { required: true, trigger: 'blur', validator: validateUsername }
        ],
        password: [
          { required: true, trigger: 'blur', validator: validatePass }
        ],
        email: [{ required: true, trigger: 'blur', validator: validateEmail }]
      },
      loading: false,
      pwdType: 'password',

      tableData: [],
      currentTableData: [],
      currentPage: 1
    }
  },

  created () {
    this.init()
  },
  methods: {
    async init () {
      this.loading = true
      try {
        const data = await user.getAllUser()
        this.tableData = data.result
        this.currentTableData = data.result.slice(0, 10)
        this.loading = false
      } catch {
        this.loading = false
      }
    },
    showPwd () {
      if (this.pwdType === 'password') {
        this.pwdType = ''
      } else {
        this.pwdType = 'password'
      }
    },

    handleSearchUser () {
      this.tableData = this.tableData.filter((item) =>
        item.user_name.includes(this.searchValue.trim())
      )
      this.currentTableData = this.tableData.slice(0, 10)
      this.currentPage = 1
    },
    handleReset () {
      this.searchValue = ''
      this.init()
      this.currentPage = 1
    },
    async handleChangeInfo () {
      if (this.title === '注册用户') {
        const res = await user.register(this.loginForm)
        if (res.errorCode === 1075) {
          this.$message.error(res.reason)
          return false
        } else {
          this.$message.success('注册成功')
          await this.init()
        }
      }
    },

    // 删除用户
    async handleDelete (index, row) {
      const confirmRlust = await this.$confirm(
        '此操作将永久删除该用户, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch((err) => err)
      if (confirmRlust === 'confirm') {
        await user.deleteUser(row.id)
        this.$message.success('删除成功')
        this.centerDialogVisible = false
        this.init()
      } else {
        this.$message.info('取消了删除!')
      }
    },
    // 编辑
    async handleCommand (command, info) {
      if (command === 'edit') {
        this.$prompt('新密码', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputErrorMessage: '密码不能为空',
          inputValidator: (value) => {
            if (!value) {
              return '输入不能为空'
            }
          }
        })
          .then(async ({ value }) => {
            await user.updatePwd(info.id, value)
            this.$message.success('修改成功')
          })
          .catch((err) => err)
      } else if (command === 'role') {
        const role = info.role === 'user' ? 'administrator' : 'user'
        await user.updateRole(info.id, role)
        this.$message.success('修改成功')
        info.role = role
      } else {
        const state = info.state === 1 ? 0 : 1
        await user.updateState(info.id, state)
        this.$message.success('修改成功')
        info.state = state
      }
    },
    // 打开弹窗
    handleDialogVisible (type, row) {
      this.centerDialogVisible = true
      if (type === 'add') {
        this.title = '注册用户'
        this.$nextTick(() => {
          this.$refs.loginForm.resetFields()
          this.$refs.loginForm.clearValidate()
        })
      } else {
        this.title = '修改'
        this.$nextTick(() => {
          this.$refs.loginForm.clearValidate()
          const res = deepClone(row)
          this.loginForm.state = +res.state
          this.loginForm.user_name = res.user_name
          this.loginForm.password = res.password
        })
      }
    },
    handleUpdataPwn () {
      this.pwnDialogVisible = true
    },
    handleCurrentChange () {
      this.currentTableData = this.tableData.slice(
        (this.currentPage - 1) * 10,
        this.currentPage * 10
      )
    }
  }
}
</script>

<style lang="less" scoped>
.password {
  vertical-align: -10px;
}
/deep/ .el-input__suffix .el-input__suffix-inner {
  cursor: pointer !important;
}
.table-container {
  margin-top: 20px;
}
</style>
